header {
  position: relative;
  z-index: 10;
  .header-top-content {
    @include flexCenter;

    padding-top: 17px;
    padding-bottom: 16px;
    height: 63px;
    box-shadow: 0px 2px 4px 0px rgb(0, 0, 0, 0.15);
    position: relative;

    &__return-arrow {
      @include flexCenter;
      height: 30px;
      width: 30px;
      position: absolute;
      left: 17px;
    }

    &__logo {
      display: flex;
      align-items: flex-end;
      height: 36px;

      &--size {
        width: 162px;
      }
    }
  }

  .header__bottom-content {
    height: 50px;
    gap: 17px;
    background: $medium-grey;

    @include flexCenter(row);
    &__icon i {
      color: $dark-grey;
      width: 12px;
      height: 16px;
    }
    &__text {
      color: $dark-grey;
      @include fontProperties("medium", 16px);
    }
  }

  @include notMobile{
    .header-background{
      height: 96px;
      width: 100%;
      background: white;
      position: absolute;
      top:0;

    }
    .header-top-content{
      height: 96px;
    }
  }
}
