.instructions {
  height: 440px;
  padding: 48px 20px 0 20px;

  &__title {
    @include fontProperties("bold", 24px);
    margin-bottom: 26px;
  }
  &__steps {
    padding-left: 12px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    .step {
      position: relative;
      &__number {
        @include fontProperties("medium", 14px);
        position: absolute;
        width: 24px;
        height: 24px;
        border-radius: 25px;
        background: $primary-color;
        color: white;
        padding-top: 5px;
        display: flex;
        justify-content: center;
        transform: translateX(-50%);
        top: 27px;
      }
      &__content {
        background: $light-grey;
        width: 100%;
        height: 72px;
        border-radius: 20px;
        box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.15);

        display: flex;
        align-items: center;
        padding-left: 33px;
        gap: 27px;

        .step__icon {
          width: 13px;
          color: $medium-grey-2;
        }

        .step__text {
          @include fontProperties("medium", 16px);
        }
      }
    }
  }

  @include notMobile {
    width: 100%;
    height: 100%;
    padding: 0;
    &__title{
      font-size: 24px;
    }

    &__steps{
      flex-direction: row;

      .step {
        width: 33%;
      }
    }
  }
}

.instructions-wrapper{
  background: white;

  .instructions {
    @include notMobile {
    
      max-width: 1080px;
    
      margin: 0 auto;
      padding-bottom: 50px;
      padding-top: 50px;
    }
  }
}